import React, { useEffect, useState } from 'react';
import './LandingPage.scss';
import { Link } from 'react-router-dom';
import { Badge, Button, Col, Input, Row } from 'reactstrap';
import MaterialIcon from '@material/react-material-icon';
import useAuth from '../../hooks/useAuth';
import useAxios from '../../hooks/useAxios';
import rupiah from '../../utils/rupiah';

const LandingPage = () => {
  const [tenderList, setTenderList] = useState([]);
  const { auth } = useAuth();

  const api = useAxios();

  useEffect(() => {
    async function fetchTenders() {
      await api
        .get(`vapi/public/tender/list`)
        .then((res) => setTenderList(res.data.data))
        .catch((err) => console.log(err));
    }

    fetchTenders();
  }, []);

  return (
    <>
      <div className="lp-container">
        <section className="lp-hero">
          <div className="navbar">
            <div className="logo"></div>
            <div className="menu">
              {/* <Link to="/">Beranda</Link>
              <Link to="/">Kontak</Link>
              <Link to="/">Tentang</Link>
              <Link to="/">Panduan</Link> */}

              {auth ? (
                <Link to="/dashboard">
                  <Button color="success">Dashboard</Button>
                </Link>
              ) : (
                <>
                  <Link to="/auth/register">
                    <Button color="primary" outline>
                      Register
                    </Button>
                  </Link>
                  <Link to="/auth/login">
                    <Button color="success">Masuk</Button>
                  </Link>
                </>
              )}
            </div>
          </div>
          <div className="title__search">
            <div className="title">
              <h2>Pelelangan Umum </h2>
              <div className="title-line" />
            </div>
            <div className="search">
              <Input type="text" placeholder="Kata kunci"></Input>
              <Input type="select">
                <option value="">Semua KKS</option>
              </Input>
              <Input type="select">
                <option value="">Bidang Usaha</option>
              </Input>
              <Button type="button" color="success">
                Cari
              </Button>
            </div>
          </div>
        </section>
        <section className="items">
          <Row>
            {tenderList.length > 0
              ? tenderList.reverse().map((t) => (
                  <Col key={t.id_tender} sm="12" lg="6" className="mb-4">
                    <div className="bg-white rounded-4 p-3 d-flex flex-column tender-card">
                      <h4 className="fw-bold">{t.nama_tender}</h4>
                      <div className="d-flex flex-column gap-2">
                        <div className="d-flex gap-2 align-items-end ">
                          <Badge className="bg-success text-white p-2 mt-2 fw-bold rounded-3">
                            Pelelangan Umum
                          </Badge>
                          <span style={{ fontSize: '14px', fontWeight: 'semi-bold' }}>
                            Oleh <strong>PT PEMBANGUNAN ACEH</strong>,
                          </span>
                          <span style={{ fontSize: '14px' }}>
                            Tayang hingga {t.batas_pendaftaran}
                          </span>
                        </div>
                        <table width="100%">
                          <tbody>
                            <tr>
                              <td>Lokasi Pengerjaan</td>
                              <td>{t.lokasi}</td>
                            </tr>
                            <tr>
                              <td>Waktu Pendaftaran</td>
                              <td>
                                {t.tgl_pendaftaran} s/d {t.batas_pendaftaran}
                              </td>
                            </tr>
                            <tr>
                              <td>Jenis Pengadaan</td>
                              <td>{t.jenis_pengadaan}</td>
                            </tr>
                            <tr>
                              <td>HPS</td>
                              <td>{rupiah(t.hps)}</td>
                            </tr>
                            <tr>
                              <td>Sistem Kualifikasi</td>
                              <td>{t.sistem_kualifikasi}</td>
                            </tr>
                          </tbody>
                        </table>
                        <div
                          style={{ display: 'flex', gap: '20px', alignItems: 'center' }}
                          className="text-danger"
                        >
                          <Link to="/auth/register">
                            <Button
                              className="rounded-3"
                              type="button"
                              color="success"
                              size="sm"
                              outline
                            >
                              Lebih Lanjut
                            </Button>
                          </Link>
                          <Button
                            className="rounded-3"
                            type="button"
                            size="sm"
                            color="danger"
                            outline
                          >
                            <div style={{ display: 'flex', gap: '5px', alignItems: 'center' }}>
                              <MaterialIcon icon="download" style={{ fontSize: '18px' }} />
                              <span>Attachment</span>
                            </div>
                          </Button>
                        </div>
                      </div>
                    </div>
                  </Col>
                ))
              : 'Loading..'}
          </Row>
        </section>
      </div>
      <section className="bg-secondary p-5 footer d-flex justify-content-between">
        <div className="d-flex gap-2">
          <MaterialIcon icon="email" />
          <span>pemasaran@ptpema.co.id</span>
        </div>
        <span>Â© 2024 All Rights Reserved. PT Pembangunan Aceh</span>
      </section>
    </>
  );
};

export default LandingPage;
